<template>
  <div 
	v-infinite-scroll="loadMore"
	class="scroll_box"
	:infinite-scroll-disabled="loading"
	:infinite-scroll-distance="10">
    <a-list :data-source="listinfo" :locale="{emptyText: '此处空空如也~~'}">
      <a-list-item slot="renderItem" slot-scope="item">
      <div class="item_box width_100 pd_left_10 pd_right_10">
      <div class="mg_top_10 mg_bottom_10 width_100 shadow_a" @click="GoDetail(item)">
		<div class="img_out_box"><div class="img_box border_radius_4"><img :src="item.pic"></div></div>
		<div class="mg_top_5 pd_left_10 pd_right_10">
		<a-row :gutter="[10,10]">
			<a-col :span="19"><div class="dispaly-ellipsis color_black_a font_size_17 bold">{{item.title}}</div></a-col>
			<a-col :span="5">
			<div class="text_align_right font_size_17 color_gray_b">
				<a-icon type="eye" theme="twoTone" twoToneColor="#d2d2d2" /><span class="pd_left_5">{{item.id}}</span>
			</div>
			</a-col>
		</a-row>
		</div>
		<div class="mg_top_5 pd_left_10 pd_right_10 pd_bottom_10">
		<a-row :gutter="[0,0]">
			<a-col :span="24"><div class="dispaly-webkit-box font_size_15 color_gray_a">{{item.description}}</div></a-col>
		</a-row>
		</div>
      </div>
  </div>
      </a-list-item>
      <div v-if="loading" class="loading_box">
        <a-spin />
      </div>
    </a-list>
  </div>
</template>
<script>
import infiniteScroll from 'vue-infinite-scroll';
import {articleList} from '../../utils/apis.js'

export default {
  directives: { infiniteScroll },
  data() {
    return {
      listinfo: [],
      loading: false,
      curpage:0,
    };
  },
  methods: {
	GetList() {
		let self = this
		let params = {
			pageSize:self.curpage,
			pageNum:999,
			categoryId:self.categoryId
		};
		articleList(params).then(res => {
			self.loading = false;
			if (self.curpage == 1) {
				self.listinfo = res.data
			} else {
				self.listinfo = self.listinfo.concat(res.data);
			}
		});
	},
    loadMore() {
		this.curpage++;
		this.loading = true;
		this.GetList();
    },
    GoDetail(curitem){
		this.$router.push({
			path: "/detail/article",
			query: {
				pid:curitem.id
			}
		});
    }
  },
  created() {
    // this.categoryle = localStorage.getItem("categoryle");
    this.categoryId='0'
    // this.GetList()
  }
};
</script>
<style lang="less" rel="stylesheet/less">
.scroll_box {
  overflow-y: auto;
  width: 100%;
  height: 800px;
}
.loading_box {
  position: absolute;
  bottom: 40px;
  width: 100%;
  text-align: center;
}
.ant-list-item {
	padding: 0;
}
.item_box {
	display: block;
}
.img_out_box {
	width: 100%;
	.img_box {
		width: 100%;
		height: 240px;
		overflow: hidden;
		img {
			width: 100%;
		}
	}
}
</style>




















