import Vue from 'vue'
import Router from 'vue-router'
import ListArticle from '../views/list/article'
import DetailArticle from '../views/detail/article'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'list_article',
      component: ListArticle
    },{
      path: '/list/article',
      name: 'list_article',
      component: ListArticle,
      meta: {
        title: 'ENJOYGO享购',
        keepAlive: true
      }
    },{
      path: '/detail/article',
      name: 'detail_article',
      component: DetailArticle,
      meta: {
        title: 'ENJOYGO享购',
        keepAlive: true
      }
    },

  ]
})