import request from './request.js'

export function articleList(params) {
    return request({
        url: '/api/home/subjectList',
        method: 'get',
        params: params
    })
}
export function articleDetail(params) {
    return request({
        url: '/api/home/subjectDetail',
        method: 'get',
        params: params
    })
}
export function getWxConfig(params) {
    return request({
        url: '/api/wx/signature',
        method: 'get',
        params: params
    })
}



