			</a-timeline>
			<div class="img_out_box text_align_center"><div class="img_box"><img src="https://cdn.jsdelivr.net/gh/objstore/static001/images/wx_qr_code.jpg"></div></div>
			<div class="text_align_center color_gray_b">扫码/长按关注“enjoygo享购买”</div>
			<div class="qrbg"><img src="https://cdn.jsdelivr.net/gh/objstore/static001/images/article_qrcode_bg.png"></div>
		</div>
		<div>
			<div id="backtopbox"><a-back-top /></div>
			<!-- <a-back-top>
			<div>top</div>
			</a-back-top> -->
		</div>
		<div class="bottomfixbar">
			<a-row>
			<a-col :span="4">
			<div class="pd_top_10 text_align_center" @click="GoList()">
				<p><a-icon type="home" :style="{ fontSize:'18px', color:'#888888'}"/></p>
				<p class="font_size_12 color_gray_b mg_top_5">首页</p>
			</div>
			</a-col>
			<a-col :span="5">
			<div class="pd_top_10 text_align_center" @click="ShareArticle()">
				<p><a-icon type="share-alt" :style="{ fontSize:'18px', color:'#888888'}"/></p>
				<p class="font_size_12 color_gray_b mg_top_5">分享</p>
			</div>
