			<a-col :span="15">
			<div class="btnsty text_align_center bg_colorful_a color_white_a">
				<p>
				<a-icon type="customer-service" :style="{ fontSize:'18px', color:'#ffffff'}"/>
				<span class="mg_left_5">留言</span>
				</p>
			</div>
			</a-col>
			</a-row>
		</div>
	</div>
</div>
</template>
<script>
import {articleDetail} from '../../utils/apis.js'
import wx from 'weixin-js-sdk'
export default {
  data() {
    return {
      detailinfo: {}
    };
  },
  methods: {
	GetDetail() {
		let self = this
		let params = {
			id:self.pid
		};
		articleDetail(params).then(res => {
			self.detailinfo = res.data
			self.ShareArticle()
		});
	},
    GoList(){
		this.$router.push({
			path: "/list/article",
			query: {
				categoryId:0
			}
		});
    },
    //分享
    ShareArticle() {
      let self = this
      let reurl = window.location.origin;
      let slink = reurl + '/detail/article?pid=' + self.pid;
      wx.ready(function () {
        wx.onMenuShareAppMessage({ // 分享给朋友
          title: self.detailinfo.title, // 分享标题
          desc: self.detailinfo.description,
          link: slink, // 分享链接
          imgUrl: self.detailinfo.pic, // 分享图标
          success: function () {
          },
          cancel: function () { }
        });
        wx.onMenuShareTimeline({ //分享到朋友圈
          title: self.detailinfo.title, // 分享标题
          link: slink,// 分享链接
          imgUrl: self.detailinfo.pic, // 分享图标
          success: function () {
          },
          cancel: function () { }
        });
      });
    },
  },
  created() {
    this.pid = this.$route.query.pid?this.$route.query.pid:6010
    this.GetDetail()
  }
};
</script>
